          box-shadow: 0 0 0 0 #667aa6 inset;
  -webkit-transition: all 3s;
  transition: all 3s;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.main {
  position: absolute;
  width: 100%;
  height: 100%;
}
.main .unlock-con {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 11000;
    width: 0;
    height: 0;
}
.main .sidebar-menu-con {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 21;
    height: 100%;
    -webkit-transition: width .3s;
    transition: width .3s;
}
.main .layout-text {
    position: absolute;
    display: inline-block;
    white-space: nowrap;
}
.main .main-hide-text .layout-text {
    display: none;
}
.main-content-container {
    position: relative;
}
.main-header-con {
    position: fixed;
    z-index: 20;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
    height: 60px;
    padding-left: 200px;
    -webkit-box-shadow: 0 2px 1px 1px rgba(100, 100, 100, 0.1);
            box-shadow: 0 2px 1px 1px rgba(100, 100, 100, 0.1);
    -webkit-transition: padding .3s;
    transition: padding .3s;
}
.main-breadcrumb {
    padding: 8px 15px 0;
}
.main-menu-left {
    height: 100%;
    background: #464c5b;
}
.main .single-page-con {
    position: absolute;
    top: 60px;
    right: 0;
    bottom: 30px;
    z-index: 1;
    overflow: auto;
    -webkit-transition: left .3s;
    transition: left .3s;
}
.main .single-page-con .single-page {
